<template>
  <div class="btnreg">
    <el-button type="info" plain @click="state.showModal=true"> <span> 답변 등록하기 </span> </el-button>
  </div>
  <div class="accordion" id="accordionPanelsStayOpenExample" >
    <!-- Answer -->
    <div class="accordion-item " v-for="(answer, i) in state.lists" :key="i">
      <h2 class="accordion-header"  v-bind:id="'panelsStayOpen-heading'+answer.answerid">
        <button class="accordion-button collapsed" type="button" data-bs-toggle="collapse"  v-bind:data-bs-target="'#panelsStayOpen-collapse'+answer.answerid" aria-expanded="true"  v-bind:aria-controls="'panelsStayOpen-collapse'+answer.answerid">
          <div class="titlecontent" v-if="answer.content != ''"> ┗ &nbsp; {{answer.content}} </div> 
          <div class="titlecontent" v-else> ┗ &nbsp; No answer</div>
        </button>
      </h2>
      <div  v-bind:id="'panelsStayOpen-collapse'+answer.answerid" class="accordion-collapse collapse"  v-bind:aria-labelledby="'panelsStayOpen-heading'+answer.answerid">
        <div class="buttongoup" v-if="state.qnickname == answer.nickname">
          <el-button-group class="ml-4">
            <el-button type="info" plain data-bs-toggle="modal" data-bs-target="#ModiModal" @click="sendId(answer.answerid, answer.nickname, answer.content, answer.rate)"> <span> 답변 수정 </span></el-button>
            <el-button type="info" plain  @click="sendId(answer.answerid, answer.nickname, answer.content, answer.rate), deleteModal()"> <span> 삭제 </span> </el-button>
          </el-button-group>
        </div>
        <div class="accordion-body">
          <!-- rate answer -->
          <div class="counter">
              <div class="buttons">
                <button  v-on:click.once="answer.rate >= 0 ? answer.rate+=1 : answer.rate+=0, sendId(answer.answerid, answer.nickname, answer.content, answer.rate), modifyAnswer()"> &#708; </button>
                <button  v-on:click.once="answer.rate != 0 ? answer.rate-=1 : answer.rate-=0, sendId(answer.answerid, answer.nickname, answer.content, answer.rate), modifyAnswer()"> &#709; </button>
              </div>
              <div class="num" ref="num">
                {{ answer.rate }}
              </div>
          </div>
          <div class="container">
            <!-- answer content -->
            <div class="content">
                <p> <b> 작성자 :: </b>  {{ answer.nickname }}</p>
                <p> <b> 답변 내용 :: </b> {{ answer.content }} </p>
                <p> <b> 수정 일자 :: </b> {{formatDate(answer.modDate)}}</p>
                <p ></p>
              </div>
          </div>
        </div>

        <!-- Comment -->
        <div class="commentList">
          <Comment
            :answerid="answer.answerid"
            :qnickname="qnickname"
            :quid="quid"
            />
        </div>
      </div>
    </div><!--accordion-item-->
  </div><!--arccodian-->
  <!-- Modal -->
  <div class="modal fade" id="ModiModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="staticBackdropLabel">답변 수정하기</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body modalcontainer">
          <div class="item">
            <label for=""> 답변 내용 :: </label><br>
            <textarea type="text" id="" v-model="modal.content" ></textarea>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">닫기</button>
          <button type="button" class="btn btn-primary"  @click="modifyAnswer()" data-bs-dismiss="modal">수정하기</button>
        </div>
      </div>
    </div>
  </div>
  <!-- Register -->
  <Teleport to="body">
    <modal :show="state.showModal" @close="state.showModal=false">
      <template #header>
        <h3>답변 등록하기</h3>
      </template>
      <template #body>
        <div class="item">
          <label for="content"> 답변 내용 :: </label><br>
          <textarea type="text" id="content" v-model="regist.registContent"></textarea>
        </div>
      </template>
      <template #footer>
        <el-button type="secondary" plain @click="state.showModal=false"> 닫기 </el-button>
        <el-button type="primary" plain @click="answerRegister()"> 등록하기 </el-button>
      </template>
    </modal>
  </Teleport>
</template>
<script>
  import { ElMessage, ElMessageBox } from 'element-plus'
  import { onMounted, toRef, ref } from 'vue'
  import { reactive, } from 'vue'
  import {useRouter} from 'vue-router';
  import Modal from './teleport/Modal.vue'
  import axios from 'axios'
  import dayjs from 'dayjs'
  import Comment from './Comment.vue'
export default {
  name: "TheAnswer",
  components: { Comment, Modal },
  props: [ 'questionid', 'qnickname', 'quid' ],
  setup(props){
  onMounted(() => { loginHandler() })
  const router = useRouter()
  const qid = toRef(props,'questionid')
  const qnickname = toRef(props,'qnickname')
  const quid = toRef(props,'quid')
  const answerPublic = ref(true)
  const formatDate = (dateString) => {
    const date = dayjs(dateString);
    return date.format('YYYY-MM-D HH:mm');
  };
  const state = reactive({
      qid : qid,
      qnickname : qnickname,
      quid: quid,
      aid : '',
      rate: '',
      nickname :'',
      answerPublic:'',
      modDate:'',
      lists: '',
      countComment:'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
      showModal   : false,
    });
  const modal = reactive({
    answerid:'',
    nickname:'',
    content:'',
    rate:'',
    answerPublic:'',
  })
  const sendId = (answerid, nickname, content, rate) =>{
    modal.answerid = answerid
    modal.nickname = nickname
    modal.content = content
    modal.rate = rate
  };
  const deleteModal= () => {
    ElMessageBox.confirm(
      '답변을 삭제하시겠습니까?',
      '알림',
      {
        confirmButtonText: '예',
        cancelButtonText: '아니오',
        type: 'warning',
      }
    ).then(() => {
        ElMessage({
          type: 'success',
          message: '삭제되었습니다.',
        })
        deleteAnswer()
      }).catch(() => {
        ElMessage({
          type: 'info',
          message: '취소되었습니다.',
        })
      })
    }
    const loginHandler = async () => {
    const url= "http://localhost:9090/api/answerlistbyqid"
    const headers = {
      'Content-Type' : 'application/json',
      Authorization: state.token,
      token: state.token,
      'name': 'test list'
    }
      const body = { question: state.qid }
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.status==200){
          state.lists = res.data
        } else{
          alert('bad.')
        }
      })
    }
    const deleteAnswer = async()=>{
    const url ='http://localhost:9090/api/deleteAnswer'
    const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token }
        const body ={answerid : modal.answerid,}
    await axios.post(url, body, {headers}).then(function(res){
      if(res.status === 200){
        setTimeout(loginHandler(),1000);
      }
    })
  }
  const modifyAnswer = async() =>{
      if(modal.content ===''){
        alert("Please input content");content.value.focus();return false;
      }
      const url = "http://localhost:9090/api/modifyAnswer"
      const headers = {
        "Content-Type" : "application/json",
        Authorization: state.token,
        "token": state.token,
      }
      const body = {
        answerid: modal.answerid,
        nickname: modal.nickname,
        content : modal.content,
        answerPublic: answerPublic.value,
        rate:modal.rate,
      }
      const response = await axios.post(url, body, {headers})
      if(response.status ===200){
        alert('성공적으로 수정되었습니다.')
        setTimeout(loginHandler(),1000);
      } else{
        alert('fail')
      }
    }
    const regist = reactive({
      registContent:'',
    });
    const answerRegister = async() => {
        if(regist.registContent === ''){
          alert('내용을 입력하세요');email.value.focus(); return false;
        }
        const url = 'http://localhost:9090/api/answerRegister'
        const headers = {
          "Content-Type" : "application/json",
          Authorization: state.token,
          "token": state.token
        }
        const body = {
          questionid : state.qid,
          uid : state.quid,
          answerPublic : answerPublic.value,
          content : regist.registContent,
        }
        const response = await axios.post(url, body, {headers})
        if(response.status === 200){
          alert('register successfully');
          state.showModal=false
          router.go(0);
        } else {
          alert('fail')
        }
      }
    return{ formatDate, answerPublic, deleteModal, state, loginHandler, deleteAnswer, modifyAnswer, modal, sendId, regist, answerRegister  }
  }
};
</script>
<style scoped>
.btnreg{
  display: block;
  text-align: right;
  margin:10px;
}
.accordion-button {
  background: rgba(6, 90, 92, 0.07);
  color: rgba(0,0,0,0.8)
}
.accordion-button:hover{
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.accordion-button:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}

button {
  font-size: 20px;
  font-weight: bold;
}
.titlecontent{
  display: block;
  width: 350px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
}
.accordion-body{
  display: flex;
  margin-top: 30px;
}
.counter{
  display:  flex;
  align-items: center;
  height: 80px;
  margin-right: 30px;
  background-color:lightgray;
  padding: 0 10px;
  border-radius: 30%;
}
.num{
  font-size: 30px;
  color: rgb(6, 90, 92);
  font-weight: bold;
  margin: 10px;
}
.buttongoup {
  display: block;
  text-align: right;
  margin:10px;
}
span{
  font-size: 15px;
}
.container{
  display: block;
  text-align: left;
}
.buttons button {
  font-size: 15px;
  display: block;
  margin: 0 auto;
  height: 10x;
  border: none;
  border-radius: 10px;
}

.modalcontainer{
  display: block;
  text-align: left;
  width: 500px;
  height: 100%px;
}
label{ margin:10px 0; }
textarea{
  width:400px;
  height: 180px;
}
</style>>

